.todo {
  width: 500px; }

.form {
  display: grid;
  grid-template-columns: 50px auto 150px;
  grid-template-rows: 50px auto;
  grid-template-areas: "priority title buttons" "none description description"; }

.todo:hover {
  background-color: #050505; }

.title {
  grid-area: title;
  padding-left: 10px;
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 20px;
  line-height: 50px; }
  .title input[name="title"] {
    font-size: 20px;
    position: relative;
    padding: 5px 5px 5px 5px;
    border: 1px solid #222222;
    top: 3px;
    left: -3px;
    height: 30px; }

.priority {
  position: relative;
  grid-area: priority;
  font-size: 25px;
  font-family: "PT Sans Narrow", sans-serif; }
  .priority span {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }

.buttons {
  grid-area: buttons; }

.buttons > div, #addbtn {
  display: inline-block;
  background-image: url("/static/todobtns.png");
  height: 26px; }

.complete {
  background-position: -54px 0px;
  width: 25px; }

.complete:hover {
  background-position: -54px -26px; }

.complete:active {
  background-position: -54px -51px; }

.uncomplete {
  background-position: -78px 0px;
  width: 30px; }

.uncomplete:hover {
  background-position: -78px -26px; }

.uncomplete:active {
  background-position: -78px -51px; }

.remove {
  background-position: -106px 0px;
  width: 23px; }

.remove:hover {
  background-position: -106px -26px; }

.remove:active {
  background-position: -106px -51px; }

.edit {
  background-position: -164px 0px;
  width: 25px; }

.edit:hover {
  background-position: -164px -26px; }

.edit:active {
  background-position: -164px -51px; }

.description {
  grid-area: description; }

.desc_view {
  position: relative;
  top: -12px;
  padding-top: 20px;
  padding-right: 10px; }

.todo input {
  border: 0;
  border-left: 3px solid #333333;
  background-color: #090909;
  color: #79797a;
  margin: 5px 10px 5px 0px;
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 20px;
  padding-left: 8px;
  height: 20px; }

.desc_edit {
  display: grid;
  grid-template-columns: auto 100px;
  grid-template-rows: auto 50px 50px;
  grid-template-areas: "text text" "priobar priobar" "color button"; }

.desc_edit > textarea {
  grid-area: text;
  min-height: 200px;
  background-color: #0b0b0b;
  border: 1px solid #222222;
  color: #79797a;
  resize: vertical;
  overflow: hidden;
  /*font-family: sans-serif;*/
  font-family: "Consolas", "Courier New", monospace;
  font-size: 13px;
  padding: 3px 3px 0px 5px; }

.save {
  grid-area: button;
  color: #464748;
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 20px; }

.save:hover {
  text-decoration: none;
  color: #a7aaac; }

.save:active {
  text-decoration: none;
  color: #e9eaec; }
