.task {
    .progressbarwrap {
        vertical-align: top; 
        padding: 10px 10px 10px 10px;
        margin: 10px 0 0 0;
    
        a {
            background-image: url('/static/btns1.png');
            display: inline-block;
        
            font-family: "PT Sans Narrow", sans-serif;
            font-size: 15px;
            color: #79797a;
        
            width: 24px;
            height: 23px;
            margin: 0 50px 0 0;
            vertical-align: bottom;
            cursor: pointer;
        }
    
        a:hover {
            color: #a7a9ac;
            text-decoration: none;
        }
    
        a span {
            padding: 0 0 0 30px;
            position: relative;
            top: 5px;
            vertical-align: bottom;
        }
    }
}

