@charset "UTF-8";
.btn-margin {
  margin: 5px 2px 5px 2px; }

body {
  background-color: #000; }

body {
  background-color: #000000;
  background-position: 0% 0px;
  font-family: arial;
  font-size: 12px;
  color: #777777; }

blockquote {
  border-left: 2px solid #333333;
  padding-left: 5px;
  margin-left: 20px; }

div {
  cursor: default; }

ul {
  padding-left: 20px; }

a {
  font-family: arial;
  color: #AAAAAA;
  text-decoration: none; }

a:hover {
  color: #FFFFFF;
  text-decoration: underline; }

#ActivityChart {
  position: absolute;
  bottom: 30px;
  left: 100px; }

#quote_cont > blockquote:before {
  content: "«"; }

#quote_cont > blockquote:after {
  content: "»"; }

#quote_cont > blockquote {
  font-family: "Georgia", serif;
  font-style: italic;
  /*margin: 10px -10px 10px -10px;*/
  border: 0;
  font-size: 40px;
  color: #17120f; }

#task_container {
  position: relative;
  top: 50px; }

#startDayButton {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
  left: 200px;
  top: -40px;
  font-weight: 400;
  font-size: 29px;
  font-family: "Ubuntu Condensed", sans-serif;
  color: #090909; }

#task_container > a:hover {
  text-decoration: none;
  color: #272a2c; }

#task_container > a:active {
  text-decoration: none;
  color: #696a6c; }

.task > * {
  display: inline-block;
  /*font-width: normal;*/
  /*font-family: "PT Sans Narrow", sans-serif;*/ }

.task .title {
  font-weight: 400;
  font-size: 25px;
  font-family: "Ubuntu Condensed", sans-serif; }

.task .duration {
  float: right;
  margin-left: 20px;
  font-size: 15px;
  font-family: "PT Sans Narrow", sans-serif; }

.task .warning {
  position: relative;
  left: -80px;
  top: 20px;
  width: 130px;
  height: 130px;
  background-image: url("/static/warning.png"); }

.task .clock {
  position: relative;
  left: 50px;
  top: 20px;
  width: 130px;
  height: 130px; }

.task .clock span {
  position: absolute;
  /*This works because position: absolute means something
    like "use top, right, bottom, left to position yourself in relation to the nearest ancestor
    who has position: absolute or position: relative*/
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 30px;
  font-width: 100;
  font-family: "PT Sans Narrow", sans-serif;
  /*transform: translate(-50%, -50%);*/ }

.task .progressbar {
  /*display: inline-block;*/
  width: 300px;
  height: 40px;
  padding: 10px 10px 10px 10px;
  background-color: #090909;
  margin: 10px 0 10px 0; }

.task .pbcont {
  vertical-align: top;
  padding: 10px 10px 10px 10px;
  margin: 10px 0 0 0; }

.task .pbcont a {
  background-image: url("/static/btns1.png");
  display: inline-block;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 15px;
  color: #79797a;
  width: 24px;
  height: 23px;
  margin: 0 50px 0 0;
  vertical-align: bottom;
  cursor: pointer; }

.task .pbcont a:hover {
  color: #a7a9ac;
  text-decoration: none; }

.task .pbcont a:active {
  color: #e7e8e9;
  text-decoration: none; }

.task .pbcont a span {
  padding: 0 0 0 30px;
  position: relative;
  top: 5px;
  vertical-align: bottom; }

.pomo {
  background-position: -78px 0px; }

.pomo:hover {
  background-position: -78px -23px; }

.pomo:active {
  background-position: -78px -46px; }

.start {
  background-position: -27px 0px; }

.start:hover {
  background-position: -27px -23px; }

.start:active {
  background-position: -27px -46px; }

.stop {
  background-position: -104px 0px; }

.stop:hover {
  background-position: -104px -23px; }

.stop:active {
  background-position: -104px -46px; }

.reset {
  background-position: -1px 0px; }

.reset:hover {
  background-position: -1px -23px; }

.reset:active {
  background-position: -1px -46px; }

.listtask {
  font-family: "Arial", sans-serif;
  padding-left: 17px;
  /*height: 20px;*/
  width: 500px; }

.listtask:hover {
  background-color: #050505; }

.listtask > * {
  vertical-align: middle; }

.listtask form > * {
  vertical-align: middle; }

.listtask .todopriority {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 50px;
  text-align: center; }

.listtask .todopriority > span {
  position: absolute;
  font-size: 25px;
  font-family: "PT Sans Narrow", sans-serif;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); }

.listtask .status_complete {
  display: inline-block;
  position: relative;
  background-image: url("/static/todobtns.png");
  width: 23px;
  height: 26px;
  margin: 12px 11px 12px 3px;
  background-position: -54px -76px; }

.listtask .status_active {
  display: inline-block;
  position: relative;
  background-image: url("/static/todobtns.png");
  width: 30px;
  height: 26px;
  margin: 12px 7px 12px 0px;
  background-position: -78px 0px; }

