.listtask .todotitle > span {
  display: inline-block;
  position: relative;
  top: 12px; }

.listtask .buttons {
  display: inline-block; }

.listtask .buttons > div, #addbtn {
  display: inline-block;
  background-image: url("/static/todobtns.png");
  /*width: 23px;*/
