.uncomplete:hover {
  background-position: -78px -26px; }

.uncomplete:active {
  background-position: -78px -51px; }

.remove {
  background-position: -106px 0px;
  width: 23px; }

.remove:hover {
  background-position: -106px -26px; }

.remove:active {
  background-position: -106px -51px; }

.edit {
  background-position: -164px 0px;
  width: 25px; }

.edit:hover {
  background-position: -164px -26px; }

.edit:active {
  background-position: -164px -51px; }

#addbtn {
  background-position: -192px 0px;
  width: 25px; }

#addbtn:hover {
  background-position: -192px -26px; }

#addbtn:active {
  background-position: -192px -51px; }

