.listtask .todopriority > a {
  position: absolute;
  display: inline-block;
  background-image: url("/static/priobtns.png");
  width: 17px;
  height: 13px;
  left: -10px; }

.listtask .todopriority .minus {
  background-position: -0px -0px;
