  top: 13px; }

#listtask_container form {
  display: block; }

#listtask_container textarea {
  background-color: #0b0b0b;
  border: 1px solid #222222;
  color: #79797a;
  /*font-family: monospace;*/
  /*font-size: 15px;*/
  /*font-family: "Ubuntu Condensed", sans-serif;*/
  /*font-size: 15px;*/ }

#listtask_container textarea {
  resize: vertical;
  overflow: hidden;
  /*font-family: sans-serif;*/
  font-family: "Consolas", "Courier New", monospace;
  font-size: 13px;
  height: 200px;
  width: 370px;
  margin-bottom: 10px;
  padding: 3px 3px 0px 5px; }

#listtask_container input, #addform input {
  border: 0;
  border-left: 3px solid #333333;
  background-color: #090909;
  color: #79797a;
  margin: 5px 10px 5px 0px;
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 20px;
  padding-left: 8px;
  height: 20px; }

#addbtn {
  margin-left: 32px;
  opacity: 0.4; }

#addform input {
  margin: 0;
  position: relative;
  height: 30px;
  top: -5px;
  left: 7px; }

#listtask_container .editpanel input[name="priority"] {
  width: 40px; }

#listtask_container .todotitle input[name="title"] {
  /*width: 295px;*/
  font-size: 20px;
  position: relative;
  padding: 5px 5px 5px 5px;
  border: 1px solid #222222;
  top: 3px;
  left: -3px;
  height: 30px; }

#listtask_container .editpanel .save {
  color: #464748;
  display: inline-block;
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 20px;
  position: relative;
  left: 300px;
  margin-bottom: 20px;
  margin-top: 15px; }

#listtask_container .editpanel .save:hover {
  text-decoration: none;
  color: #a7aaac; }

#listtask_container .editpanel .save:active {
  text-decoration: none;
  color: #e9eaec; }

#listtask_container .editpanel .priobar {
  height: 8px;
  width: 300px; }

.listtask .exp {
  margin-left: 47px;
  position: relative; }

/*.listtask .exp .edit{
    position: absolute;
    left: -40px;
}*/
.listtask .exp .descpanel {
  position: relative;
  top: -12px;
  padding-top: 20px;
  padding-right: 10px; }

.listtask .edit_show {
  display: none; }

.listtask .exp > * {
  /*font-family: Calibri, Arial, sans-serif;*/
