#volumeContainer {
  position: absolute;
  top: 20px;
  left: 15px;
  /*margin-top:20px;*/
  /*margin-left:15px;*/
  cursor: pointer; }

#volumeContainer > * {
  vertical-align: middle; }

#volumeControl {
  display: inline-block;
  /*position:relative;
    top: 30px;
    left: 30px;*/
  width: 100px;
  height: 6px; }

#volumeIcon {
  background-image: url("/static/btns1.png");
  display: inline-block;
  width: 26px;
  height: 23px;
  margin: 0 3px 0 0;
  opacity: 0.2;
  cursor: default;
  /*padding: 0 0 0 30px;*/
  /*background-position: -132px -0px;*/ }

#volumeContainer .level3 {
  background-position: -132px -0px; }

#volumeContainer .level2 {
  background-position: -132px -23px; }

#volumeContainer .level1 {
  background-position: -132px -46px; }

#volumeContainer .mute {
  background-position: -160px -0px; }

#daystrip {
  position: absolute;
  top: 20px;
  left: 630px; }

#daystrip > div {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 25px;
  background-color: #444444;
  vertical-align: middle;
  margin: 0 5px 5px 0;
  border: 3px solid #777777; }

/*#daystrip .cday > * {
    vertical-align: middle;
}
*/
#daystrip .MISSED {
  background-color: #080808;
  border: 3px solid #0a0a0a;
  color: #111111; }

#daystrip .COMPLETE {
  background-color: #105010;
  border: 3px solid #107010;
  color: #209020; }

#daystrip .REST {
  background-color: #501049;
  border: 3px solid #701054;
  color: #902089; }

#daystrip > div > span {
  font-family: "Arial", sans-serif;
  font-weight: bold;
  font-size: 15px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-55%, -55%); }

#timeline {
  position: absolute;
  top: 70px;
  left: 355px; }

#timeline > .bar {
  height: 430px;
  width: 4px;
  border-radius: 3px;
  background-color: #090909;
  padding: 3px 3px 3px 3px; }

#waterblock {
  position: absolute;
  top: 20px;
  left: 560px; }

#waterblock > .barcont {
  height: 230px;
  width: 10px;
  border-radius: 9px;
  background-color: #090909;
  padding: 10px 10px 10px 10px; }

#waterblock .buttons {
  width: 50px;
  position: relative;
  left: -2px;
  top: 20px; }

#waterblock .buttons > a {
  display: inline-block;
  /*background-image: url('/static/todobtns.png');*/
  width: 50px;
  /*height: 50px;*/
  /*line-height: 100p-x;*/
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 15px;
  color: #6d6e71;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

#waterblock .buttons > a:hover {
  text-decoration: none;
  color: #a7a9ac; }

#waterblock .buttons > a:active {
  text-decoration: none;
  color: #e7e8e9; }

#TimerContainer {
  position: absolute;
  top: 30px;
  right: 100px; }

#TimerContainer .usertimer {
  font-size: 30px; }

#TimerContainer .expired {
  color: #FF0000; }

#TimerContainer .buttons {
  display: inline-block; }

