@media only screen and (max-width: 500px) {
  /*#startDayButton { position: relative; left: 0; top: 0; }*/
  #task_container {
    display: none; }
  #waterblock {
    display: none; }
  #daystrip {
    display: none; }
  #timeline {
    display: none;
    position: absolute;
    top: 70px;
    left: 355px; }
  #volumeContainer {
    position: absolute;
    top: 20px;
    left: auto;
    right: 15px; }
  #ActivityChart {
    position: static;
    left: auto;
    top: auto;
    bottom: auto; }
  #listtask_container {
    /*background-color: #ff0000;*/
    position: static;
    left: 0px;
    top: 0px; }
  .listtask {
    width: auto;
    max-width: 450px; }
  .listtask .todotitle {
    width: auto;
    /*font-family: "Ubuntu Condensed", sans-serif;*/
    /*font-size: 20px;*/ }
  .listtask .buttons {
    display: none;
    /*float: right;*/ } }
